/* uncomment to see outlines of stuff when you hover *-/
:hover {
  background:rgba(0, 0, 0, 0.1);
}/**/
.giant{font-size: 10em;
    color: #138edb;
    position: relative;
    margin-left:135px
  
  }
  .giants{font-size: 10em;
    color: #89f442;
  position: relative;
    margin-left:5px
  }
  .giantss{font-size: 10em;
    background: -webkit-linear-gradient(290deg, #89f442, #138edb);
    background: -o-linear-gradient(290deg, #89f442, #138edb);
    background: linear-gradient(160deg, #89f442, #138edb);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  position: relative;
    margin-left:80px
  }
  html,
  body {
    margin: 0;
    padding: 0;
  }
  
  .section {
    width: 100%;
  }
  
  .container2 {
    position: relative;
    width: 1170px;
    margin: 0 auto;
    color: #444;
    font-size: 14px;
    font-weight: 300;
    font-family: 'Monaco', Roboto, 'Open Sans', Arial, sans-serif;
    overflow: hidden;
  }
  
  .section .container2 {
    padding: 30px 0 50px 0;
  }
  
  .section.bg {
    background: #f7f7f7;
    color: black
  }
  /*
    Header
  */
  
  .hold {
    height: 80px;
  }
  
  .header {
    line-height: 80px;
    width: 100%;
    -webkit-transition: line-height 0.2s linear, -webkit-box-shadow 0.2s linear;
    transition: line-height 0.2s linear, -webkit-box-shadow 0.2s linear;
    -o-transition: line-height 0.2s linear, box-shadow 0.2s linear;
    transition: line-height 0.2s linear, box-shadow 0.2s linear;
    transition: line-height 0.2s linear, box-shadow 0.2s linear, -webkit-box-shadow 0.2s linear;
    position: relative;
    top: 0;
    left: 0;
    z-index: 100;
    background: #138edb;
  }
  
  .header.small {
    line-height: 50px;
    -webkit-box-shadow: 0px 1px 3px 0px rgba(50, 50, 50, 0.8);
            box-shadow: 0px 1px 3px 0px rgba(50, 50, 50, 0.8);
  }
  
  .header.small > .container2 > #logo {
    height: 40px;
  }
  
  #logo {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    background: #89f442;
    float: left;
    height: 40px;
    width: 170px;
    margin-left: 5px;
  }
  
  ul.nav {
    float: right;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  ul.nav li {
    float: left;
    position: relative;
  }
  
  ul.nav li a {
    -webkit-transition: color 0.2s linear;
    -o-transition: color 0.2s linear;
    transition: color 0.2s linear;
    font-size: 18px;
  }
  
  ul.nav li:hover a {
    color: black;
  }
  
  ul.nav li a {
    padding: 21px;
    color: #89f442;
    text-decoration: initial;
  }
  /*
    Slider
  */
  
  .section .slider,
  .section .footer {
    background: #138edb;
  }
  
  .slidercontent {
    text-align: center;
  }
  
  .hero {
    font-family: 'Roboto Slab', sans-serif;
    color: initial;
    font-weight: normal;
    letter-spacing: 4px;
  
  }
  
  h1.hero {
    font-size: 54px;
  }
  
  h2.hero {
    font-size: 30px;
    margin-bottom: 60px;
  }
  
  h1.hero:after {
    content: "";
    width: 300px;
    position: relative;
    border-bottom: 1px solid #aaa;
    text-align: center;
    margin: auto;
    margin-top: 15px;
  }
  
  .call {
    color: white;
    display: block;
    margin-bottom: 20px;
  }
  
  .call span {
    display: inline;
    border: 1px solid white;
    padding: 8px 13px;
    font-size: 20px;
    -webkit-transition: background 0.15s linear;
    -o-transition: background 0.15s linear;
    transition: background 0.15s linear;
  }
  
  .call span:hover {
    background: rgba(255, 255, 255, 0.1);
    cursor: pointer;
  }
  /* 
    Columns 
  */
  
  .col {
    float: left;
    padding: 0;
    margin: 0;
    position: relative;
  }
  
  .col.four {
    width: 23%;
    margin: 0 1%;
  }
  
  .col.three {
    width: 31.3%;
    margin: 0 1%;
  }
  
  .col.two {
    width: 40%;
    margin: 0 2.5%;
    padding: 0 2.5%;
  }
  
  .col.extrapad {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  
  .col .service,
  .col .feature {
    font-size: 21px;
    font-weight: 10000;
    margin-left: 60px;
    font-family: 'Roboto Slab', sans-serif;
  }
  
  .col .service:after {
    content: "";
    width: 50px;
    position: center;
    border-bottom: 1px solid #eee;
    display: block;
    text-align: center;
    margin: auto;
    margin-top: 15px;
  }
  
  .col .feature {
    font-size: 19px;
  }
  
  .col h1.side,
  .col p.side,
  .col span.side:first-of-type {
    margin-left: 50px;
    text-align: left;
  }
  
  .col .icon {
    border-radius: 50%;
    height: 85px;
    width: 85px;
    line-height: 85px;
    text-align: center;
    margin: 0 auto;
    -webkit-transition: background 0.25s linear, color 0.25s linear;
    -o-transition: background 0.25s linear, color 0.25s linear;
    transition: background 0.25s linear, color 0.25s linear;
  }
  
  .col .icon.side {
    position: absolute;
    padding: 0;
    margin: 0;
    top: -15px;
    height: 50px;
    width: 50px;
  }
  
  .col:hover > .icon {
    background: #89f442;
    color: white;
  }
  
  .col:hover > .icon.side {
    background: initial;
    color: initial;
  }
  
  .responsivegroup {
    display: none;
  }
  /*
    Column specifics
  */
  
  .col p,
  .col h1 {
    padding: 0 1%;
    text-align: center;
  }
  
  .group.margin {
    margin-bottom: 20px;
  }
  
  .col .imgholder {
    height: 300px;
    width: 100%;
    background: #333;
    -webkit-transition: background 0.3s linear;
    -o-transition: background 0.3s linear;
    transition: background 0.3s linear;
  }
  
  .col.bg {
    background: #FFF;
  }
  
  .col.pointer {
    cursor: pointer;
  }
  
  .col.bg:hover .imgholder {
    background: #555;
  }
  
  .col span.feature {
    font-size: 20px;
  }
  /*
    Text
  */
  
  .container2 > h1:not(.hero) {
    margin-bottom: 30px;
    text-align: center;
  }
  
  .container2 > h1:after {
    content: "";
    width: 30px;
    position: relative;
    border-bottom: 1px solid #aaa;
    display: block;
    text-align: center;
    margin: auto;
    margin-top: 15px;
  }
  
  h2 {
    font-family: 'Roboto Slab', sans-serif;
    text-align: center;
    font-weight: 400;
    font-size: 18px;
  }
  
  .left,
  .left > h1,
  .left > p {
    text-align: left;
  }
  
  .reset {
    text-align: left !important;
  }
  
  .reset:after {
    display: none !important;
  }
  /* 
    Slider with Content
  */
  
  .white h1,
  .white h2,
  .white p,
  .white div,
  .white a {
    color: initial;
  }
  /*
    Responsive
  */
  
  .group:after {
    content: "";
    display: table;
    clear: both;
  }
  
  @media all and (max-width: 768px) {
    .container2 {
      width: 95%;
    }
    .col.four {
      width: 48%;
      margin: 1%;
    }
    .col.three {
      display: block;
      width: 95%;
      padding: 0;
      margin: 0 auto;
      float: none;
    }
    .header {
      height: auto;
      background: #eee;
    }
    #logo {
      position: initial;
      float: none;
      display: block;
      -webkit-transform: none;
          -ms-transform: none;
              transform: none;
      margin: 10px auto 0 auto;
    }
    ul.nav {
      float: none;
      display: block;
      text-align: center;
      margin: 0 auto;
    }
    ul.nav li {
      float: initial;
      display: inline-block;
    }
    .responsivegroup {
      display: block;
    }
    .responsivegroup:after {
      content: "";
      display: table;
      clear: both;
    }
  }
  
  @media all and (min-width: 768px) {
    .container2 {
      
      width: 750px;
    }
  }
  
  @media all and (min-width: 992px) {
    .container2 {
      width: 970px;
    }
  }
  
  @media all and (min-width: 1200px) {
    .container2 {
      width: 1170px;
    }
  }
  
  @media all and (max-width:450px) {
    .col, .col.four, .col.three, .col.two {
      display: block;
      width: 95%;
      padding: 0;
      margin: 0 auto;
      float: none;
    }
    .col.extrapad {
      padding: 1%;
      margin-bottom: 10px;
    }
    .group {
      display: none;
    }
  }
  
  .container2.slidercontent {
   background: -webkit-linear-gradient(45deg, #89f442, #138edb);
   background: -o-linear-gradient(45deg, #89f442, #138edb);
   background: linear-gradient(45deg, #89f442, #138edb); 
  }


